import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import QvipleLoader from "../../Loader/QvipleLoader";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import InstituteAnalyticProfile from "./InstituteAnalyticProfile";

const StaffTodayAttendance = lazy(() =>
  import("./StaffTodayAttendance/StaffTodayAttendance")
);
const StaffTimetable = lazy(() => import("./StaffTimetable/StaffTimetable"));
const StaffTeachingPlan = lazy(() =>
  import("./StaffTeachingPlan/StaffTeachingPlan")
);

const StudentAttendanceClass = lazy(() =>
  import("./StudentAttendanceClass/StudentAttendanceClass")
);
const StudentAttendance = lazy(() =>
  import("./StudentAttendance/StudentAttendance")
);
const LectureStudentAttendance = lazy(() =>
  import("./StudentAttendance/LectureStudentAttendance")
);
const InstituteAnalytic = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ? (
              <InstituteAnalyticProfile />
            ) : searchParams.get("type") === "today-attendance" ? (
              <StaffTodayAttendance instituteId={getQuery.state?.instituteId} />
            ) : searchParams.get("type") === "timetable" ? (
              <StaffTimetable instituteId={getQuery.state?.instituteId} />
            ) : searchParams.get("type") === "teaching-plan" ? (
              <StaffTeachingPlan instituteId={getQuery.state?.instituteId} />
            ) : searchParams.get("type") === "student-attendance" ? (
              <StudentAttendanceClass
                instituteId={getQuery.state?.instituteId}
              />
            ) : searchParams.get("type") === "rsstudent" ? (
              <StudentAttendance />
            ) : searchParams.get("type") === "student" ? (
              <LectureStudentAttendance />
            ) : null}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteAnalytic;
