import { useLocation, useParams } from "react-router-dom";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import {
  assestsStaffUrl,
  // assestsUserFinanceUrl,
  assestsUserLmsUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { memo } from "react";

const InstituteAnalyticProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  let measure = document.getElementById("562nbhgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      <UserMemberTabsWrapper>
        <SquareTabWrapper dynamicId="562nbhgh">
          <SquareTabCard
            label="staff_attendance_only"
            iconUrl={`${assestsUserLmsUrl}/attendance.svg`}
            url={`/q/${params.username}/institute/member/analytic?type=today-attendance`}
            state={getQuery.state}
            isAuthorized
            isVisible={{
              bank_details: true,
            }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="staff_timetable"
            iconUrl={`${assestsStaffUrl}/timetable.svg`}
            url={`/q/${params.username}/institute/member/analytic?type=timetable`}
            state={getQuery.state}
            isAuthorized
            isVisible={{
              pending_fees: true,
            }}
            authorizedKey="pending_fees"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="staff_teaching_plan"
            iconUrl={`${assestsStaffUrl}/academic.svg`}
            url={`/q/${params.username}/institute/member/analytic?type=teaching-plan`}
            state={getQuery.state}
            isAuthorized
            isVisible={{
              admission_pending_fees: true,
            }}
            authorizedKey="admission_pending_fees"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="student_attendance_only"
            iconUrl={`${assestsUserLmsUrl}/attendance.svg`}
            url={`/q/${params.username}/institute/member/analytic?type=student-attendance`}
            state={getQuery.state}
            isAuthorized
            isVisible={{
              bank_details: true,
            }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
        </SquareTabWrapper>
      </UserMemberTabsWrapper>
    </>
  );
};

export default memo(InstituteAnalyticProfile);
